



















	export default {
		data() {
			return {
				page:''
			};
		},
    onLoad(options) {
    	this.page=options.page
    },
    methods:{
      goPage(e) {
        let path = e.currentTarget.dataset.path
        let page = e.currentTarget.dataset.page||''
      	uni.navigateTo({
      		url: path+'?page='+page
      	})
      }
    }
	}
